html {
  font-size: 16px;
}

body {
  width: 100%;
  height: 100%;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.maincontent {
  height: 100%;
  min-height: 650px;
  transition: 1s;
  will-change: transform;
}

.section {
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.container {
  margin: 0;
  padding: 0;
  min-width: 320px;
  max-width: 940px;
  margin: 0 auto;
  padding: 0 10px;
  display: block;
  position: relative;
  min-height: 650px;
}

.we-do {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 650px;
  width: 100%;
  background-color: #111114;
  background-image: url(../img/bg1.jpg);
  min-height: 100vh;
}

.header__nav--link {
  text-decoration: none;
  font-family: Ubuntu, Arial, sans-serif;
  color: white;
  font-size: 0.8rem;
  font-weight: 300;
  line-height: 1.8rem;
  text-transform: uppercase;
  white-space: nowrap;
}

.header__nav--link:hover {
  text-decoration: underline;
}

.header__nav--btn {
  background-color: #e35028;
  border: none;
  border-radius: 4px;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 16px;
  padding-right: 16px;
  text-decoration: none;
  font-family: Ubuntu, Arial, sans-serif;
  color: white;
  font-size: 0.8rem;
  font-weight: 300;
  line-height: 1.8rem;
  line-height: 0;
  text-transform: uppercase;
  display: block;
  text-align: center;
}

.header__nav--btn:hover {
  background-color: #c1401e;
}

.header__nav--hamburger {
  position: absolute;
  text-decoration: none;
  display: none;
  width: 25px;
  height: 5px;
  background-color: white;
  border-radius: 5px;
}

.header__nav--hamburger::before {
  content: "";
  position: absolute;
  top: 200%;
  width: 25px;
  height: 5px;
  background-color: white;
  border-radius: 5px;
}

.header__nav--hamburger::after {
  content: "";
  position: absolute;
  top: -200%;
  width: 25px;
  height: 5px;
  background-color: white;
  border-radius: 5px;
}

.we-do__text--first {
  font-family: Gagalin, Arial, sans-serif;
  color: #f9b43b;
  font-size: 4.5rem;
  font-weight: 400;
  line-height: 5.3rem;
}

.we-do__text--second {
  font-family: Lifehack, Arial, sans-serif;
  color: white;
  font-size: 4rem;
  font-weight: 500;
  line-height: 3.7rem;
  text-align: center;
}

.header__nav--list {
  list-style: none;
}

/*Стилизация Breadcrumbs*/

.breadcrumbs__list {
  list-style: none;
}

.breadcrumbs__link {
  width: 5px;
  height: 5px;
  display: block;
  position: relative;
}

.breadcrumbs__link:after {
  position: absolute;
  content: "";
  background: white;
  color: white;
  height: 6px;
  width: 6px;
  display: block;
  border-radius: 50%;
}

.breadcrumbs__link:hover::before {
  content: "";
  border: 1px solid white;
  height: 16px;
  width: 16px;
  display: block;
  border-radius: 50%;
  position: absolute;
  top: -6px;
  left: -6px;
}

.breadcrumbs__link:active::before {
  content: "";
  border: 1px solid white;
  height: 16px;
  width: 16px;
  display: block;
  border-radius: 50%;
  position: absolute;
  top: -6px;
  left: -6px;
}

.breadcrumbs__item {
  margin-bottom: 300%;
}

/*Стилизация Breadcrumbs close*/

.header__logo {
  z-index: 51;
}

.we-do__next-step--link {
  display: block;
  background-image: url(../img/arrow-scroll.svg);
  background-repeat: no-repeat;
  background-position: center center;
  width: 1.875rem;
  height: 1rem;
}

/*Сетки*/

.header {
  display: flex;
  margin-bottom: -5%;
}

.header__nav--list {
  display: flex;
