}

.header__nav--item {
  position: relative;
  margin-right: 2.4rem;
}

.header__nav--item:last-child {
  margin-right: 0;
}

.header__nav--item::before {
  content: "";
  position: absolute;
  background-color: white;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  top: 13px;
  left: -19px;
}

.header__nav--item:first-child::before {
  display: none;
}

.header__nav--btn-wrap {
  margin-left: 22px;
  padding-top: 34px;
}

.we-do__content {
  display: flex;
}

.we-do__img {
  margin-left: 4%;
