/*popup nav*/

.nav-popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2f3234;
  opacity: 0.7;
  display: none;
  z-index: 50;
}

.nav-popup--list {
  list-style: none;
}

.nav-popup--item {
  position: relative;
}

.nav-popup--item::before {
  content: "";
  position: absolute;
  background-color: white;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  top: -60%;
  left: 50%;
}

.nav-popup--item:first-child::before {
  display: none;
}

.nav-popup__btn {
  position: absolute;
  top: 0.35%;
  right: 14%;
  width: 20px;
  height: 20px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  box-sizing: border-box;
  background-image: url(../img/btnclose.png);
  background-repeat: no-repeat;
  outline: none;
}

