/*Стилизация чекбоксов и радиобаттонов*/

.form__radio,
.form__checkbox {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.form__block {
  cursor: pointer;
  position: relative;
  flex: 1;
  margin-right: 20px;
}

.form__block:last-child {
  margin-right: 0;
}

.form__block input[type="radio"] + .radio-indicator {
  position: absolute;
  top: 23px;
  left: -25px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: white;
}

.form__block input[type="radio"]:checked + .radio-indicator::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #e35028;
}

.form__block input[type="checkbox"] + .checkbox-indicator {
  position: absolute;
  top: 5px;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: white;
}

.form__block input[type="checkbox"]:checked + .checkbox-indicator::before,
.form__block input[type="checkbox"]:checked + .checkbox-indicator::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 2px;
  background-repeat: no-repeat;
  width: 11px;
  height: 11px;
  background-image: url(../img/check.png);
}

